import { connect } from 'react-redux';
import UserList from './UserList';
import {
  addFilmToFavoritesActionCreator,
  markAsWatchedActionCreator,
} from '../../redux/userList/userListActions';

const mapStateToProps = state => ({
  favoritesList: state.favoritesList,
  watchedList: state.watchedList,
});

const mapDispatchToProps = dispatch => ({
  addFilmToFavorites: film => dispatch(addFilmToFavoritesActionCreator(film)),
  markAsWatched: film => dispatch(markAsWatchedActionCreator(film)),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(UserList);
